import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SecurityAccessGuard } from '../../common/guards/security-access.guard';

import { Bio001Component } from './bio001/bio001.component';
import { Bio002Component } from './bio002/bio002.component';
import { Bio003Component } from './bio003/bio003.component';
import { Bio004Component } from './bio004/bio004.component';

const routes: Routes = [
  {
    path: 'bio001',
    component: Bio001Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'BIO001'
    }
  },
  {
    path: 'bio002',
    component: Bio002Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'BIO002'
    }
  },
  {
    path: 'bio003',
    component: Bio003Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'BIO003'
    }
  },
  {
    path: 'bio004',
    component: Bio004Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'BIO004'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BiometriaRoutingModule { }
